.fullstack-container{
    background-size: cover !important;
    background-repeat: no-repeat !important;
}
.fullstack-bg::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgb(255,255,255);
    background: radial-gradient(circle at center center, rgba(255,255,255,0) 0%, rgba(0,0,0,0.8211659663865546) 0%);
    z-index: -1; /* Ensure the overlay is behind the content */
    border-radius: 16px;
}