.spline {
  position: relative;
  width: 100%;
  padding-top: 133.33%;
  height: auto !important;
}
.spline canvas {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100% !important;
  height: 100% !important;
}
.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 3rem;
}
.job{
  font-size: clamp(5vw, 48px, 39px) !important;
  font-weight: 800;
}
.box{
  flex-basis: 400px;
  border: 1px solid gray;
  color: white;
  border-radius: 16px;
  min-height: 300px;
  padding: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.entering{
  opacity: 0;
  transform: translate3d(0, -18rem, 0);
  -webkit-animation: fadein 1.2s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: fadein 1.2s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
  animation-timeline: view();
}
@-webkit-keyframes fadein {
  30%,100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
@keyframes fadein {
  30%,100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
@media (max-width: 767px)   {
  .spline {
    width: 50% !important;
    padding-top: 63.33% !important; /* 4/3 of the width to maintain a 3:4 ratio */
  }
  .gap-5{
    gap: 0.5rem !important;
  }
  .row-loader{
    max-height: fit-content !important;
  }
  .spline-container{
    max-height: fit-content !important;
  }
}
